@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,700);
/*General Styling*/
body, html{
	height: 100%;
	margin: 0;
	font-family: 'Poppins', sans-serif;
	color: antiquewhite;
}

/*Background Styling*/
.bg::before {
	content: "";
	background-position: center;
	position: absolute;
	background-repeat: no-repeat;
	background-size: cover;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-image: url(/weather-app3/static/media/bg.54eac887.jpeg);
	-webkit-filter: blur(10px);
	        filter: blur(10px);
}

/* Main Widget Styling*/

.container{
	margin: 15% 30% 30% 50%;
	position: absolute;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

.widget{
	width: 450px;
	height: 250px;
	border-radius: 20px;
	background: rgba(299, 299, 299, 0.3);
}

.widget .left{
	position: absolute;
	left: 4%;
	width: 200px;
}

.widget .right{
	position: absolute;
	right: 0;
	width: 200px;
	color: #fff;
	margin: 50px 0;
}

.widget .right .city{
	font-size: 1.5em;
	text-align: center;
	margin-top: 2%;
	margin-bottom: 0%;
	margin-left: -20%;
	text-shadow: 1px 1px 5px #707070;
}

.weather-status{
	color: #fff;
	text-align: center;
	margin-top: 0;
}

.widget .right .degree{
	font-size: 2.2em;
	font-weight: bold;
	text-align: center;
	margin: 0;
	text-shadow: 1px 1px 5px #707070;
}

.widget .bottom{
	width: 100%;
	position: absolute;
	bottom: 10px;
	display:-webkit-flex;
	display:flex;
	-webkit-justify-content: center;
	        justify-content: center;
	color: #fff;
}

.widget .bottom div{
	margin: 5px 10px 5px 10px;
	text-align: center;
	line-height: 100%;
	font-size: 0.83em;
	text-shadow: 1px 1px 5px #707070;
}

