/* SearchBox Styling*/

.search1{  
	padding-top: 5%;
	position: relative;
}

.flexbox {
	width: 100%;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.search {
	margin-bottom: -8%;
}

.search > h3 {
	font-weight: normal;
}

.search > h1,
.search > h3 {
	color: white;
	margin-bottom: 0px;
	text-shadow: 0 1px #ffffff;
}

.search > .inp {
	display: inline-block;
	position:relative;
	-webkit-filter: drop-shadow(0 1px #ffffff);
	        filter: drop-shadow(0 1px #ffffff);
}

.search > .inp:after {
	content: "";
	background: white;
	width: 5px;
	height: 15px;
	position: absolute;
	top: 35px;
	right: 0.5px;
	-webkit-transform: rotate(130deg);
	        transform: rotate(130deg);
}

.search > .inp > input {
	color: white;
	font-size: 16px;
	background: transparent;
	width: 12px;
	height: 12px;
	padding: 13px;
	border: dotted 3px white;
	outline: none;
	border-radius: 35px;
	transition: width 0.2s;
}

.search > .inp > input::-webkit-input-placeholder {
	color: #efefef;
	opacity: 0;
	-webkit-transition: opacity 150ms ease-out;
	transition: opacity 150ms ease-out;
}

.search > .inp > input:-ms-input-placeholder {
	color: #efefef;
	opacity: 0;
	-ms-transition: opacity 150ms ease-out;
	transition: opacity 150ms ease-out;
}

.search > .inp > input::placeholder {
	color: #efefef;
	opacity: 0;
	transition: opacity 150ms ease-out;
}

.search > .inp > input:focus::-webkit-input-placeholder {
	opacity: 0.6;
}

.search > .inp > input:focus:-ms-input-placeholder {
	opacity: 0.6;
}

.search > .inp > input:focus::placeholder {
	opacity: 0.6;
}

.search > .inp > input:not(:-ms-input-placeholder) {
	width: 150px;
}

.search > .inp > input:focus,
.search > .inp > input:not(:placeholder-shown) {
	width: 150px;
}

